@-webkit-keyframes logo {
  0% {
    width: 80px;
    height: 80px;
    border-radius: 100%;
    left: 50%;
    opacity: 1;
  }

  25% {
    width: 0;
    transform: translate(-50%, -50%) rotate(-60deg);
    height: 0;
    left: 50%;
    opacity: 1;
  }

  45% {
    width: 80px;
    height: 80px;
    transform: translate(-50%, -50%) rotate(-30deg);
    border-radius: 0;
    left: 50%;
    opacity: 1;
  }

  65% {
